import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import linkState from 'linkstate';
import { Text, Localizer, withText } from 'preact-i18n';

import mu from '../../lib/musher-util';
const MARGIN_TOP = mu.marginTop(true);
const MARGIN_BOTTOM = mu.marginBottom();

const initialState = {
	isLoading: false,
};

@observer
class Teams extends Component {
    toggleTeam = (team) => {
		const { callback } = this.props;
        const { workoutStore } = this.props.stores;
		const { currentTeam } = workoutStore;
		if (team === currentTeam) {
			workoutStore.setCurrentTeam(undefined);
		} else {
			workoutStore.setCurrentTeam(team);
		}
		if (util.isDefined(callback)) {
			callback();
		}
	}

    render() {
        const { userStore, workoutStore } = this.props.stores;
        const { teams } = userStore;
		const { currentTeam } = workoutStore;
        return (
			<div class='container-fluid px-0'>
				<div class='row'>
					{teams && teams.map(team => (
						<div class='col-6 mb-2'>
							<div class={`border rounded rounded-pill px-3 py-2 ${currentTeam === team.id ? 'bg-success' : ''}`}
								style='line-height: 0.9em; cursor: pointer; pointer-events: inherit;'
								onClick={() => this.toggleTeam(team.id)}
							>
								<small>
									<i class='fas fa-users mr-2' />
									{team.name}
								</small>
							</div>
						</div>
					))}
				</div>
			</div>
        );
    }
}

@observer
class Calendar extends Component {
	setFocus = () => {
		if (this.inputRef) {
			this.inputRef.focus();
		}
	}
	editMonth = (e) => {
		const month = e.target.parentElement.dataset.month || e.target.dataset.month;
		const year = e.target.parentElement.dataset.year || e.target.dataset.year;
		const distance = e.target.parentElement.dataset.distance || e.target.dataset.distance;
		// console.log({ month, year });
		this.setState({
			showInput: true,
			inputYear: year,
			inputMonth: month,
			inputDay: null,
			inputWeek: null,
			newDistance: distance || null,
		}, () => this.setFocus());
	}

	editWeek = (e) => {
		const week = e.target.parentElement.dataset.week || e.target.dataset.week;
		const year = e.target.parentElement.dataset.year || e.target.dataset.year;
		const distance = e.target.parentElement.dataset.distance || e.target.dataset.distance;
		// console.log({ week, year });
		this.setState({
			showInput: true,
			inputYear: year,
			inputWeek: week,
			inputMonth: null,
			inputDay: null,
			newDistance: distance || null,
		}, () => this.setFocus());
	}

	editDay = (e) => {
		const day = e.target.parentElement.dataset.day || e.target.dataset.day;
		const month = e.target.parentElement.dataset.month || e.target.dataset.month;
		const year = e.target.parentElement.dataset.year || e.target.dataset.year;
		const week = e.target.parentElement.dataset.week || e.target.dataset.week;
		const distance = e.target.parentElement.dataset.distance || e.target.dataset.distance;
		// console.log({ day, month, year });
		this.setState({
			showInput: true,
			inputYear: year,
			inputMonth: month,
			inputDay: day,
			inputWeek: week,
			newDistance: distance || null,
		}, () => this.setFocus());
	}

	saveDistance = async () => {
		const { workoutPlanStore, workoutStore, userStore } = this.props.stores;
		const { currentTeam } = workoutStore;
		const teamName = userStore.findTeam(currentTeam) || {};
		const { year } = this.props;
		const { workoutPlans } = workoutPlanStore;
		const workoutPlanCurrent = workoutPlans[0] || {};
		const { inputYear, inputMonth, inputWeek, inputDay, newDistance } = this.state;
		// console.log({ showInput, inputYear, inputMonth, inputWeek, inputDay, newDistance });

		const currentMonth = new Date().getMonth() + 1;
		const currentYear = util.getYear();
		const seasonStart = parseInt(year || (currentMonth < 6 ? currentYear - 1 : currentYear), 10);

		if (!workoutPlanCurrent.id) {
			const response = await workoutPlanStore.insert({
				title: `Workout plan for ${seasonStart} - ${seasonStart + 1}: ${teamName}`,
				team: currentTeam,
				seasonYear: seasonStart,
				seasonMonth: 6,
			});
			const { id: newId } = response.data;
			await workoutPlanStore.load(newId);
		} else {
			await workoutPlanStore.load(workoutPlanCurrent.id);
		}
		const { workoutPlan } = workoutPlanStore;
		const { id } = workoutPlan;
		// console.log({ id });

		let field;
		if (inputYear) {
			if (inputDay) {
				field = 'days';
			} else {
				field = 'weeks';
			}
		}
		await workoutPlanStore.saveField(id, field, {
			year: inputYear,
			month: inputMonth,
			day: inputDay,
			week: inputWeek,
			distance: newDistance,
		});
		await workoutPlanStore.load(id);

		this.resetInput();
	}

	resetInput = () => {
		this.setState({
			showInput: false,
			inputYear: null,
			inputMonth: null,
			inputDay: null,
			inputWeek: null,
			newDistance: null,
		});
	}

	checkKeydown = (e) => {
		switch (e.key) {
			case 'Enter':
				this.saveDistance();
				break;

			case 'Escape':
				this.resetInput();
				break;
		
			default:
				break;
		}
	}

	render() {
		const { appState, workoutPlanStore, userStore } = this.props.stores;
		const { user } = userStore;
		const { language = 'no' } = user;
		const { darkmode } = appState;
		const DAYS = mu.getDays(language);
		const MONTHS = mu.getMonths(language);

		const { showInput, inputYear, inputMonth, inputWeek, inputDay, newDistance } = this.state;

        const currentMonth = new Date().getMonth() + 1;
		const currentEpoch = new Date().getTime();
		const { month = currentMonth, year = util.getYear(), dog, basehref, showNavigation = true } = this.props;

        const monthNum = parseInt(month, 10);
        const yearNum = parseInt(year, 10);
        const { month: nextMonth, year: nextYear } = mu.getNextMonth(monthNum, yearNum);
        const { month: prevMonth, year: prevYear } = mu.getPrevMonth(monthNum, yearNum);

        const daysOfMonth = util.getDaysInMonth(month, year);
        const weeksInMonth = util.getWeeksInMonth(month, year);
        const firstWeek = weeksInMonth[0].week;
        const daysOfWeek = util.getDaysInWeek(firstWeek, year);
        let totalDistanceKmMonth = 0;
		let totalDistanceKmMonthPlan = 0;
		let totalWorkoutDistanceKmMonth = 0;

		const summaryMonth = workoutPlanStore.findWorkoutPlanSummaryMonth(year, monthNum) || {};

		return (
			<>
                <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-4'>
                    {showNavigation && <a href={`${basehref || '/workouts/month/'}${nextMonth}/${nextYear}/skipScroll`} class='float-right btn btn-sm btn-link'><i class='fas fa-arrow-right' /></a>}
                    {showNavigation && <a href={`${basehref || '/workouts/month/'}${prevMonth}/${prevYear}/skipScroll`} class='float-left btn btn-sm btn-link'><i class='fas fa-arrow-left' /></a>}
                    <h5><Text id='workout.plan.month'>Mned</Text> - {MONTHS[month - 1]} {year} {summaryMonth.distance ? `- ${util.format(summaryMonth.distance, 0)} km` : ''}</h5>
                    {dog && <div>{dog.name}</div>}
                    <small>{util.isoDate(daysOfMonth[0], false, false, true)} - {util.isoDate(daysOfMonth[daysOfMonth.length - 1], false, false, true)}</small>
                </div>
                <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-4'>
					{(showInput && inputYear && inputWeek && !inputDay) && (
						<>
							<div class='form-inline w-100'>
								<div class='form-group w-75 mr-3'>
									<input class='form-control w-100 form-control-lg' type='text' 
										placeholder={`Enter distance for ${inputYear} w${inputWeek}`}
										ref={c => this.inputRef = c}
										onInput={linkState(this, 'newDistance')}
										onKeydown={this.checkKeydown}
										autocomplete='off'
										value={newDistance}
									/>
								</div>
								<button type='button' class='btn btn-primary' onClick={this.saveDistance}><Text id='workout.plan.save'>Lagre</Text></button>
							</div>
						</>
					)}
					{(showInput && inputYear && inputMonth) && (
						<>
							<div class='form-inline w-100'>
								<div class='form-group w-75 mr-3'>
									<input class='form-control w-100 form-control-lg' type='text'
										placeholder={`Enter distance for ${inputYear}-${util.padDate(inputMonth)} ${inputDay ? `-${util.padDate(inputDay)}` : ''}`}
										ref={c => this.inputRef = c}
										onInput={linkState(this, 'newDistance')}
										onKeydown={this.checkKeydown}
										autocomplete='off'
										value={newDistance}
									/>
								</div>
								<button type='button' class='btn btn-primary' onClick={this.saveDistance}><Text id='workout.plan.save'>Lagre</Text></button>
							</div>
						</>
					)}
				</div>
                <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mt-4'>
                    <table class='table table-striped table-sm'>
                        <thead>
                            <tr>
                                <th class='text-muted'><small><Text id='workout.plan.week'>Uke</Text></small></th>
                                {daysOfWeek.map(day => <th><small>{DAYS[day.getDay()]}</small></th>)}
                                <th class='text-muted'><small><Text id='workout.plan.sum'>Sum</Text></small></th>
                                <th class='text-muted'><small><Text id='workout.plan.planned'>Plan</Text></small></th>
                                {/* <th class='text-muted'>Totalt</th> */}
                            </tr>
                        </thead>
                        <tbody>
                            {weeksInMonth && weeksInMonth.map(weekObj => {
                                const daysOfWeek = util.getDaysInWeek(weekObj.week, year);
								const summaryWeek = workoutPlanStore.findWorkoutPlanSummaryWeek(year, weekObj.week) || {};
								const weekEpoch = daysOfWeek[6].getTime();
								const isWeekInTheFuture = weekEpoch > currentEpoch;
                                let totalWeek = 0;
                                let totalWorkoutWeek = 0;
								if (summaryWeek.distance > 0) {
	                                totalDistanceKmMonthPlan += summaryWeek.distance;
								}
                                return (
                                    <tr>
                                        <td class={`text-right text-muted pt-3 border-right ${!inputDay && inputYear == year && inputWeek == weekObj.week ? 'border rounded border-primary' : ''}`}>
                                            <small onClick={this.editWeek} data-week={weekObj.week} data-year={year} data-distance={summaryWeek.distance}>
                                                {weekObj.week}
                                            </small>
                                        </td>
                                        {daysOfWeek.map(day => {
											const yyyy = day.getFullYear();
											const mm = day.getMonth() + 1;
											const dd = day.getDate();
											const epoch = day.getTime();
											const isDayInTheFuture = epoch > currentEpoch;
                                            const summaryWorkout = userStore.findWorkoutSummaryWeekDay((isDayInTheFuture ? yyyy - 1 : yyyy), mm, dd) || {};
                                            const summary = workoutPlanStore.findWorkoutPlanSummaryDay(yyyy, mm, dd) || {};
                                            if (summary && summary.distance) {
                                                totalWeek += summary.distance;
												totalDistanceKmMonth += summary.distance;
                                            }
											if (summaryWorkout.distanceKm) {
												totalWorkoutWeek += summaryWorkout.distanceKm;
												totalWorkoutDistanceKmMonth += summaryWorkout.distanceKm;
											}
                                            // console.log(summary);
                                            // count: 1, day: 5, distanceKm: 10, duration: 3600, elevation: 200, month: 9, speedAvg: 12, year: 2020
                                            return (
                                                <td class={`text-center font-weight-light position-relative pt-3 pb-3 border-right ${inputYear == yyyy && inputMonth == mm && inputDay == dd ? 'border rounded border-primary' : ''}`}
                                                    style={`background-color: ${mu.getDayColor(day.getDay(), darkmode)}; font-size: 0.8em;`}
													onClick={this.editDay} data-day={day.getDate()} data-month={day.getMonth() + 1} data-year={year} data-week={weekObj.week} data-distance={summary.distance}
												>
                                                    <div class='position-absolute text-muted' style='top: 0px; right: 2px; font-size: 0.7em;'>
                                                        {day.getDate()}
                                                    </div>
                                                    {summary && summary.distance ? 
                                                        <div class='rounded-pill w-100 px-1'
                                                            style={`background-color: ${mu.getPlanColor(summary, darkmode)};`}
                                                        >
                                                            {util.format(summary.distance, 0)}
                                                            {summary.count > 1 && <span class='position-absolute text-danger' style='bottom: 0; right: 0px; font-size: 0.6em;'>{summary.count}</span>}
                                                        </div> : '-'}
													{summaryWorkout.distanceKm > 0 && (
                                                        <span class={`position-absolute text-${isDayInTheFuture ? 'muted' : 'success'}`}
                                                            style='bottom: 0; left: 0px; font-size: 0.85em;'>
                                                            {util.format(summaryWorkout.distanceKm, 1)}
                                                        </span>
                                                    )}

                                                </td>
                                            );
                                        })}
                                        <td class='text-right pt-3 pb-3 position-relative border-right' style='font-size: 0.8em;'>
											{util.format(totalWeek, 0)}
											{totalWorkoutWeek > 0 && (
												<span class={`position-absolute text-${isWeekInTheFuture ? 'muted' : 'success'}`}
                                                    style='bottom: 0; left: 0px; font-size: 0.85em;'>
                                                    {util.format(totalWorkoutWeek, 0)}
                                                </span>
                                            )}
										</td>
                                        <td class='text-right pt-3 pb-3 text-muted border-right' style='font-size: 0.8em;'
											onClick={this.editWeek} data-week={weekObj.week} data-year={year} data-distance={summaryWeek.distance}
										>
											{summaryWeek.distance ? util.format(summaryWeek.distance, 0) : ''}
										</td>
                                        {/* <td class='text-right text-muted pt-3'>{totalDistanceKmMonth}</td> */}
                                    </tr>
                                );
                            })}
                        </tbody>
                        <tfoot>
                            <tr>
                                <td colspan='8' class='text-right pb-3'><Text id='workout.plan.totalkm'>Totalt km</Text></td>
                                <td class='text-right position-relative pb-3'>
									{util.format(totalDistanceKmMonth, 0)}
									{totalWorkoutDistanceKmMonth > 0 && (
										<span class='position-absolute text-success'
											style='bottom: 0; left: 0px; font-size: 0.65em;'>
											{util.format(totalWorkoutDistanceKmMonth, 0)}
										</span>
									)}
								</td>
                                <td class='text-right text-muted'>{util.format(totalDistanceKmMonthPlan, 0)}</td>
                            </tr>
                        </tfoot>
                    </table>
				</div>
			</>
		);
	}
}

@observer
class WorkoutPlans extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

	loadAll = (props = this.props) => {
		this.loadWorkoutPlans(props);
		this.loadWorkoutSummary(props);
	}

	async loadWorkoutSummary(props = this.props) {
        this.setState({ isLoading: true });
		const { userStore, workoutStore } = this.props.stores;
		const { currentTeam } = workoutStore;

        const currentMonth = new Date().getMonth() + 1;
        const { month = currentMonth, year = util.getYear(), team = currentTeam, dog = {} } = props;
        await userStore.getWorkoutSummary({ team, dog: dog.id, daysBack: 500 });
        await userStore.getWorkoutSummaryDay({ daysBack: 500, team, dog: dog.id });
        this.setState({ isLoading: false });
    }

	loadWorkoutPlans = async (props = this.props) => {
		this.setState({ isLoading: true });
		const { year } = props;

		const currentMonth = new Date().getMonth() + 1;
		const currentYear = util.getYear();
		const seasonStart = parseInt(year || (currentMonth < 6 ? currentYear - 1 : currentYear), 10);
		const { workoutPlanStore, workoutStore } = props.stores;
		const { currentTeam } = workoutStore;

		workoutPlanStore.updateItem('workoutPlans', []);
		workoutPlanStore.updateItem('workoutPlan', {});

		await workoutPlanStore.load({ query: {
			team: currentTeam,
			seasonYear: seasonStart,
			seasonMonth: 6,
		} });
		const { workoutPlans } = workoutPlanStore;
		const workoutPlanCurrent = workoutPlans[0] || {};
		if (workoutPlanCurrent.id) {
			await workoutPlanStore.load(workoutPlanCurrent.id);
		}
		this.setState({ isLoading: false });
	}

    componentDidMount() {
		this.loadWorkoutPlans();
		this.loadWorkoutSummary();
    }

    componentWillReceiveProps(nextProps) {
        // console.log('componentWillReceiveProps', nextProps);
        if (nextProps.month !== this.props.month
            || nextProps.year !== this.props.year
            || nextProps.team !== this.props.team) {
            this.loadWorkoutPlans(nextProps);
            this.loadWorkoutSummary(nextProps);
        }
    }

	render() {
		const { isLoading } = this.state;
		const { userStore, appState, workoutPlanStore, workoutStore } = this.props.stores;
		const { workoutPlan } = workoutPlanStore;
		const { currentTeam } = workoutStore;
		const { workoutSummaryWeekDay } = userStore;

		const { darkmode } = appState;
		const { message, year } = this.props;
		const {
			isAdmin,
		} = userStore;

		const currentMonth = new Date().getMonth() + 1;
		const currentYear = util.getYear();
		const seasonStart = parseInt(year || (currentMonth < 6 ? currentYear - 1 : currentYear), 10);

		const nextYear = seasonStart + 1;
		const prevYear = seasonStart - 1;

		const months = util.monthRange(`${seasonStart}-06-01`, `${seasonStart + 1}-05-31`);
		return (
			<div class='container-fluid' style={`margin-bottom: ${MARGIN_BOTTOM}; margin-top: ${MARGIN_TOP};`}>
				<div class='row mb-4 fixed-top' style={`margin-top: 58px; background-color: ${darkmode ? '#191d21' : '#f8f9fa'};`}>
					<div class='col text-center'>
						<a href='/workouts/' class='text-secondary stretched-link'><i class='fas fa-chart-line' /> <Text id='workout.plan.dashboard'>Oversikt</Text></a>
					</div>
					<div class='col text-center'>
						<a href='/workouts/list' class='text-secondary stretched-link'><i class='fas fa-list' /> <Text id='workout.plan.list'>Liste</Text></a>
					</div>
					<div class='col text-center border-bottom border-primary'>
						<a href='/workouts/plan' class='text-primary stretched-link'><i class='fas fa-calendar-alt' /> <Text id='workout.plan.plan'>Plan</Text></a>
					</div>
				</div>

				{message && (
					<div class='alert alert-success mb-3' role='alert'>
						<i class='fas fa-check' /> {message}
					</div>
				)}
				
				<div class='row'>
					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 border-bottom mb-5'>
						<Teams stores={this.props.stores} callback={this.loadAll} />
					</div>
					{/* <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>

                        <div class='text-center mt-4 mb-4 text-muted'>
                            <div class='display-1'><i class='fab fa-pagelines' /></div>
    						Treningsplan kommer ganske snart :)
                        </div>
					</div> */}
				</div>

				<div class='row'>
					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 mb-2 text-center'>		
						<a href={`/workouts/plan/${nextYear}/skipScroll`} class='float-right btn btn-sm btn-link'><i class='fas fa-arrow-right' /></a>
						<a href={`/workouts/plan/${prevYear}/skipScroll`} class='float-left btn btn-sm btn-link'><i class='fas fa-arrow-left' /></a>
						<h5><Text id='workout.plan.season'>Sesongen</Text> {seasonStart} - {seasonStart + 1}</h5>
						<small>{workoutPlan.title}</small>
					</div>
				</div>

				{months.map(m => (
					<div>
						<Calendar stores={this.props.stores} basehref='/workouts/plan/' month={m.month} year={m.year} key={`${workoutPlan.id || seasonStart}-${currentTeam}-${workoutSummaryWeekDay.length}`} showNavigation={false} />
					</div>
				))}
			</div>
		);
	}
}

export default WorkoutPlans;
