import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer, withText } from 'preact-i18n';

import mu from '../../lib/musher-util';

import LineGraph from '../../components/lineGraph';

@withText((props) => ({
    week: <Text id='workout.graph.week' />,
    distance: <Text id='workout.graph.distance' />,
    ascend: <Text id='workout.graph.ascend' />,
    plan: <Text id='workout.graph.plan' />,
}))
@observer
class WorkoutGraph extends Component {
    render() {
        const { userStore, workoutPlanStore } = this.props.stores;
        const { isLoading, week, distance, ascend, plan, year } = this.props;
        const {
            user,
			workoutSummary,
			graphWorkoutSummaryWeekDistanceCurrent, graphWorkoutSummaryWeekDistancePrev,
			graphWorkoutSummaryWeekElevationCurrent, graphWorkoutSummaryWeekElevationPrev,
		} = userStore;
        const { language = 'no' } = user;
		const MONTHS = mu.getMonths(language);

        const { graphPlanWeekDistanceCurrent } = workoutPlanStore;

        const currentMonth = new Date().getMonth() + 1;
		const currentYear = util.getYear();
		const seasonStart = parseInt(year || (currentMonth < 6 ? currentYear - 1 : currentYear), 10);

        // const summaryMonth = workoutPlanStore.findWorkoutPlanSummaryMonth(year, monthNum) || {};
        const seasonMonths = util.monthRange(`${seasonStart}-06-01`, `${seasonStart + 1}-05-31`);
        const prevSeasonMonths = util.monthRange(`${seasonStart - 1}-06-01`, `${seasonStart}-05-31`);
        const seasonWeeks = util.weekRange(`${seasonStart}-06-01`, `${seasonStart + 1}-05-31`);

        const xTicksAll = seasonWeeks.map(wo => wo.week);
        const parts = xTicksAll.length % 6;
        const partSize = Math.floor(xTicksAll.length / parts);
        const xTicks = [];
        for (let i = 0, l = xTicksAll.length; i < l; i += partSize) {
            xTicks.push(xTicksAll[i]);
        }

        return (
            <>
                <h5><Text id='workout.graph.title'>Treningsstatistikk</Text> {seasonStart} - {seasonStart + 1}</h5>
                <div class='w-100 mt-3 mb-4'>
                    {/* <xmp>graphWorkoutSummaryWeekDistanceCurrent: {JSON.stringify(graphWorkoutSummaryWeekDistanceCurrent, null, 4)}</xmp>
                    <xmp>graphWorkoutSummaryWeekDistancePrev: {JSON.stringify(graphWorkoutSummaryWeekDistancePrev, null, 4)}</xmp> */}
                    <small class='mb-4'><small>
                        <table class='table table-sm font-weigth-lighter border-bottom'>
                            <thead>
                                <tr>
                                    <th style='padding: .15rem;'><Text id='workout.graph.season'>Sesong</Text></th>
                                    {seasonMonths.map(m => (
                                        <th class='text-right' style='padding: .15rem;'><a href={`/workouts/month/${m.month}/${m.year}`}>{MONTHS[m.month - 1]}</a></th>
                                    ))}
                                </tr>
                            </thead>
                            <tbody>
                                <tr>
                                    <th scope='row' style='padding: .15rem;'>{seasonStart - 1} - {seasonStart}</th>
                                    {prevSeasonMonths.map(m => {
                                        // const summaryMonth = workoutPlanStore.findWorkoutPlanSummaryMonth(m.year, m.month) || {};
                                        return (
                                            <>
                                                <td class='text-right' style='padding: .15rem;'>
                                                    {/* <nobr>{summaryMonth.distance}</nobr><br /> */}
                                                    <nobr>{userStore.findWorkoutSummaryMonth(m.year, m.month)}</nobr><br />
                                                    <nobr>{userStore.findWorkoutSummaryMonth(m.year, m.month, 'elevation')}</nobr><br />
                                                </td>
                                            </>
                                        );
                                    })}
                                    <td class='text-muted' style='padding: .15rem;'>
                                        <nobr><i class='fas fa-road' /> km</nobr><br />
                                        <nobr><i class='fas fa-mountain' /> m</nobr><br />
                                    </td>
                                </tr>
                                <tr>
                                    <th scope='row' style='padding: .15rem;'>{seasonStart} - {seasonStart + 1}</th>
                                    {seasonMonths.map(m => {
                                        // const summaryMonth = workoutPlanStore.findWorkoutPlanSummaryMonth(m.year, m.month) || {};
                                        return (
                                            <>
                                                <td class='text-right' style='padding: .15rem;'>
                                                    <nobr>{userStore.findWorkoutSummaryMonth(m.year, m.month)}</nobr><br />
                                                    <nobr>{userStore.findWorkoutSummaryMonth(m.year, m.month, 'elevation')}</nobr><br />
                                                </td>
                                            </>
                                        );
                                    })}
                                    <td class='text-muted' style='padding: .15rem;'>
                                        <nobr><i class='fas fa-road' /> km</nobr><br />
                                        <nobr><i class='fas fa-mountain' /> m</nobr><br />
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                    </small></small>

                    <LineGraph
                        stores={this.props.stores}
                        width={500}
                        height={150}
                        showXTicks={1}
                        xTicksVal={val => `${week} ${val}`}
                        xTicks={xTicks}
                        paddingTop={30}
                        lineFromXzero={true}
                        lineFromXmax={true}
                        xTicksLink={'/workouts/week/'}

                        dataLeft={[graphWorkoutSummaryWeekDistanceCurrent, graphWorkoutSummaryWeekDistancePrev, graphPlanWeekDistanceCurrent]}
                        legendLeft={[`${seasonStart} ${distance}`, `${seasonStart - 1} ${distance}`, `${seasonStart} ${plan}`]}
                        // yMaxLeft={30}
                        // yMinLeft={0}
                        showYTicksLeft={1}
                        yTicksLeftPostfix={'km'}
                        unitLeft={['km', 'km']}
                        avgWindowLeft={[0, 0]}
                        smoothLeft={[1, 1]}

                        dataRight={[graphWorkoutSummaryWeekElevationCurrent, graphWorkoutSummaryWeekElevationPrev]}
                        legendRight={[`${seasonStart} ${ascend}`, `${seasonStart - 1} ${ascend}`]}
                        showYTicksRight={1}
                        yTicksRightPostfix={'m'}
                        unitRight={['m', 'm']}
                        avgWindowRight={[0, 0]}
                        smoothRight={[1, 1]}

                        // marker={week}
                    />
                </div>

                {!isLoading && workoutSummary && workoutSummary.length < 3 && (
                    <div class='mb-5 py-3 border-top border-bottom'>
                        <div class='text-left' style='line-height: 1.0em;'>
                            <div class='display-4 float-left text-warning mr-3'>
                                <i class='fas fa-paw' />
                            </div>
                            <small>
                                <Text id='workout.graph.too-little-data'>Forelpig er det litt lite data for  kunne vise noen fine grafer. 
                                Etterhvert som du legger inn flere treninger s vil dette bli en fin tabell og graf som viser progresjonen din.</Text>
                            </small>
                        </div>
                    </div>
                )}
            </>
        );
    }
}

export default WorkoutGraph;
