const VACCINE_TYPES = {
    rabies: {
        name: 'Rabies (3 r)',
        duration: 365 * 3,
    },
    deworming: {
        name: 'Ormekur - reise',
        duration: 28,
    },
    BbPi: {
        name: 'BbPi - Nesevaksine (1 r)',
        duration: 365 * 1,
    },
    DHP: {
        name: 'DHP (3 r)',
        duration: 365 * 3,
    },
    DHPPi: {
        name: 'DHPPi (3 r)',
        duration: 365 * 3,
    },
};

class MusherUtil {
    static getDayColor(day, darkmode) {
        if (RED_DAYS.indexOf(day) > -1) {
            if (darkmode) {
                return '#501515';
            }
            return '#ffdddd';
        }
        return '';
    }

    static getTrainingColor(summary, darkmode) {
        if (summary.distanceKm > 0 || summary.distance > 0) {
            if (darkmode) {
                return '#154015';
            }
            return '#aaffaa';
        }
        return '';
    }

    static getPlanColor(summary, darkmode) {
        if (summary.distanceKm > 0 || summary.distance > 0) {
            if (darkmode) {
                return '#0052cc';
            }
            return '#b3d1ff';
        }
        return '';
    }

    static getHistoryTypeColor(historyType) {
        const colorName = HISTORY_TYPE_COLOR_MAP[historyType];
        if (typeof colorName  === 'number') {
            return colorName
        }
        return COLOR_NAMES[colorName];
    }

    static avgArr(array) {
        if (Array.isArray(array) && array.length > 0) {
            return array.reduce((a, b) => a + b) / array.length;
        }
        return 0;
    }

    static getNextMonth(month, year) {
        const result = { month, year };
        if ((month + 1) > 12) {
            result.month = 1;
            result.year += 1;
        } else {
            result.month += 1;
        }
        return result;
    }

    static getPrevMonth(month, year) {
        const result = { month, year };
        if ((month - 1) < 1) {
            result.month = 12;
            result.year -= 1;
        } else {
            result.month -= 1;
        }
        return result;
    }

    static getNextWeek(week, year) {
        const result = { week, year };
        if ((week + 1) > 53) {
            result.week = 1;
            result.year += 1;
        } else {
            result.week += 1;
        }
        return result;
    }

    static getPrevWeek(week, year) {
        const result = { week, year };
        if ((week - 1) < 1) {
            result.week = 53;
            result.year -= 1;
        } else {
            result.week -= 1;
        }
        return result;
    }

    static getHistoryMap() {
        return HISTORY_TYPE_MAP;
    }

    static getVaccineTypes() {
        return VACCINE_TYPES;
    }

    static getHistoryPri(num) {
        switch (num) {
            case 1:
                return HISTORY_PRIORITY_LIST_1;
            case 2:
                return HISTORY_PRIORITY_LIST_2;
            case 3:
                return HISTORY_PRIORITY_LIST_3;
            default:
                return HISTORY_PRIORITY_LIST_1;
        }
    }

    static speedAvg(obj) {
        let speedAvg = 0;
        if (obj.duration > 0 && obj.distanceKm > 0) {
            const rest = obj.rest || 0;
            const duration = obj.duration - rest;
            speedAvg = obj.distanceKm / (duration / 3600);
        }
        return speedAvg;
    }

    static getDays(lang = 'no') {
        return DAYS[lang];
    }

    static getMonths(lang = 'no') {
        return MONTHS[lang];
    }

    static marginTop(hasSubMenu = false) {
        if (hasSubMenu) {
            return MARGIN_TOP_W_SUBMENU;
        }
        return MARGIN_TOP;
    }

    static marginTopBack(hasSubMenu = false) {
        if (hasSubMenu) {
            return MARGIN_TOP_BACK_W_SUBMENU;
        }
        return MARGIN_TOP_BACK;
    }

    static marginBottom() {
        return MARGIN_BOTTOM;
    }
}

module.exports = MusherUtil;
